    border-radius: 25px;
    text-align: center;
    margin-bottom: 30px;
}

.categories .item .thumb {
    position: relative;
    border-radius: 25px;
    overflow: hidden;
}

.categories .item h4 {
    font-size: 17px;
    font-weight: 600;
    color: #fff;
    padding: 22px;
    margin-bottom: 0px;
}

/* 
---------------------------------------------
CTA Style
--------------------------------------------- 
*/

.cta {
    position: relative;
}

.cta::after {
    background-image: url(../images/cta-bg.jpg);
    max-width: 550px;
    max-height: 510px;
    width: 100%;
    height: 100%;
    position: absolute;
    content: "";
    border-radius: 25px;
    left: 50%;
    transform: translateX(-275px);
    top: 0;
    z-index: -1;
}

.cta .shop {
    margin-top: 80px;
    background-color: #f7f7f7;
    border-radius: 25px;
    padding: 80px;
}

.cta .shop .section-heading {
    margin-right: 30px;
    margin-bottom: 50px;
}

.cta .shop p {
    font-weight: 600;
    font-size: 16px;
}

.cta .shop .main-button {
    margin-top: 50px;
}

.cta .subscribe {
    background-color: #f7f7f7;
    border-radius: 25px;
    padding: 80px 60px;
}

.cta .subscribe .section-heading {
    margin-bottom: 50px;
}

.cta .subscribe form {
    position: relative;
    max-width: 100%;
}

.cta .subscribe form input {
    max-width: 100%;
    width: 100%;
    height: 50px;
    outline: none;
    border-radius: 25px;
    background-color: #fff;
    border: none;
    padding: 0px 25px;
    font-size: 14px;
    color: #7a7a7a;
}

.cta .subscribe form button {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    background-color: #ee626b;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 500;
    padding: 0px 25px;
    border: none;
    border-radius: 25px;
    position: absolute;
    right: 0;
    top: 0;
    transition: all 0.3s;
}

.cta .subscribe form button:hover {
    background-color: #0071f8;
}

/* 
---------------------------------------------
Footer Style
--------------------------------------------- 
*/

footer {
    margin-top: 150px;
    background-color: #0071f8;
    background-repeat: no-repeat;
    background-size: cover;
    background-image: url(../images/footer-bg.jpg);
    min-height: 150px;
    border-radius: 150px 150px 0px 0px;
}

footer p {
    text-align: center;
    line-height: 90px;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
}

footer p a {
    color: #fff;
    transition: all 0.3s;
    position: relative;
    z-index: 3;
}

footer p a:hover {
    opacity: 0.75;
}

/* 
---------------------------------------------
Page Header Style
--------------------------------------------- 
*/

.page-heading {
    background-image: url(../images/page-heading-bg.jpg);
    border-radius: 0px 0px 150px 150px;
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: cover;
    padding: 200px 0px 100px 0px;
    text-align: center;
}

.page-heading span {
    display: inline-block;
    font-size: 16px;
    color: #fff;
}

.page-heading span a {
    color: #fff;
}

.page-heading h3 {
    font-size: 48px;
    text-transform: uppercase;
    color: #fff;
    margin-top: 20px;
    position: relative;
    margin-bottom: 20px;
}

/* 
---------------------------------------------
Shop Page Style
--------------------------------------------- 
*/

.trending ul.trending-filter {
    list-style: none;
    text-align: center;
    margin-bottom: 70px;
}

.trending ul.trending-filter li {
    display: inline-block;
    margin: 5px 8px;
}

.trending ul.trending-filter li a {
    display: inline-block;
    text-align: center;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 600;
    color: #1e1e1e;
    background-color: #eee;
    padding: 8px 20px;
    border-radius: 25px;
    transition: all 0.3s;
}

.trending ul.trending-filter li a.is_active {
    background-color: #ee626b;
    color: #fff;
}

.trending ul.trending-filter li a.is_active:hover {
    color: #fff;
}

.trending ul.trending-filter li a:hover {
    color: #ee626b;
}

.trending-box .item {
    background-color: #eeeeee;
    position: relative;
    overflow: hidden;
    border-radius: 25px;
    margin-bottom: 30px;
}

.trending-box .item .thumb {
    position: relative;
    border-radius: 25px;
    overflow: hidden;
}

.trending-box .item span.price em {
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    text-decoration: line-through;
    display: block;
}

.trending-box .item span.price {
    text-align: right;
    position: absolute;
    right: 20px;
    top: 20px;
    border-radius: 10px;
    background-color: #008af8;
    font-size: 17px;
    text-transform: uppercase;
    font-weight: 600;
    color: #fff;
    padding: 8px 15px 6px 15px;
}

.trending-box .item .down-content {
    padding: 25px;
}

.trending-box .item .down-content span.category {
    font-size: 14px;
    color: #7a7a7a;
    margin-bottom: 5px;
    display: inline-block;
}

.trending-box .item .down-content h4 {
    font-size: 17px;
    font-weight: 600;
    transition: all 0.3s;
}

.trending-box .item .down-content a {
    position: absolute;
    display: inline-block;
    text-align: center;
    line-height: 40px;
    width: 40px;
    height: 40px;
    background-color: #ee626b;
    color: #fff;
    border-radius: 50%;
    right: 25px;
    bottom: 25px;
    transition: all 0.3s;
}

.trending-box .item:hover .down-content h4 {
    color: #0071f8;
}

.trending-box .item:hover .down-content a {
    background-color: #0071f8;
}

.trending ul.pagination {
    margin-top: 50px;
    text-align: center;
    width: 100%;
    display: inline-block;
}

.trending ul.pagination li {
    display: inline-block;
    margin: 0px 5px;
}

.trending ul.pagination li a {
    display: inline-block;
    width: 40px;
    height: 40px;
    line-height: 40px;
    text-align: center;
    background-color: #eee;
    color: #1e1e1e;
    font-size: 15px;
    font-weight: 600;
    border-radius: 50%;
    transition: all 0.3s;
}

.trending ul.pagination li a:hover,
.trending ul.pagination li a.is_active {
    background-color: #0071f8;
    color: #fff;
}

/* 
---------------------------------------------
Single Page Style
--------------------------------------------- 
*/

.single-product .sep {
    width: 100%;
    height: 1px;
    background-color: #eee;
    margin: 80px 0px;
}

.single-product .left-image {
    margin-right: 80px;
}

.single-product .left-image img {
    border-radius: 25px;
}

.single-product h4 {
    font-size: 27px;
    margin-bottom: 20px;
}

.single-product span.price {
    font-size: 27px;
    font-weight: 700;
    color: #0071f8;
}

.single-product span.price em {
    font-style: normal;
    color: #c8c8c8;
    font-size: 17px;
    text-decoration: line-through;
    font-weight: 500;
    margin-right: 5px;
}

.single-product p {
    margin-top: 50px;
    margin-bottom: 60px;
}

.single-product form input {
    width: 80px;
    height: 50px;
    background-color: #f7f7f7;
    border: 1px solid #e7e7e7;
    border-radius: 25px;
    text-align: center;
    float: left;
    margin-right: 15px;
}

.single-product form {
    display: inline;
}

.single-product form button {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    background-color: #ee626b;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 500;
    padding: 0px 25px;
    border: none;
    border-radius: 25px;
    transition: all 0.3s;
}

.single-product form button i {
    margin-right: 5px;
}

.single-product form button:hover {
    background-color: #0071f8;
}

.single-product ul {
    margin-top: 60px;
}

.single-product ul li {
    display: block;
    margin: 20px 0px;
    font-size: 14px;
    color: #0071f8;
}

.single-product ul li:last-child {
    margin-bottom: 0px;
}

.single-product ul li span {
    width: 120px;
    display: inline-block;
    color: #aaa;
}

.tabs-content {
    background-color: #f7f7f7;
    border-radius: 25px;
    padding: 60px;
    transition: all 0.3s;
}

.tabs-content .nav-link {
    border: none;
    border-radius: 0px;
    background-color: transparent !important;
    font-size: 20px;
    font-weight: 600;
    color: #1e1e1e;
    padding: 0px;
}

.tabs-content ul.nav-tabs {
    border-bottom: none;
}

.tabs-content ul.nav-tabs li {
    border-right: 1px solid #d7d7d7;
    margin-right: 30px;
    padding-right: 30px;
}

.tabs-content ul.nav-tabs li:last-child {
    border-right: none;
    margin-right: 0px;
    padding-right: 0px;
}

.tabs-content .nav-tabs .nav-link.active {
    color: #0071f8;
}

.tabs-content .tab-content {
    margin-top: 30px;
}

.related-games .main-button {
    text-align: right;
    margin-top: 32px;
}

/* 
---------------------------------------------
Contact PAge Style
--------------------------------------------- 
*/

.contact-page .left-text {
    background-color: #f7f7f7;
    border-radius: 25px;
    padding: 60px;
}

.contact-page .left-text .section-heading {
    margin-bottom: 50px;
}

.contact-page .left-text p {
    margin-bottom: 50px;
}

.contact-page .left-text ul li {
    display: block;
    font-size: 16px;
    color: #aaaaaa;
    font-size: 300;
    margin-bottom: 30px;
}

.contact-page .left-text ul li:last-child {
    margin-bottom: 0px;
}

.contact-page .left-text ul li span {
    display: block;
    font-size: 14px;
    font-weight: 600;
    color: #4a4a4a;
    margin-bottom: 5px;
}

.contact-page .right-content {
    margin-left: 50px;
}

.contact-page .right-content #map {
    margin-bottom: 60px;
}

.contact-page .right-content form input {
    width: 100%;
    height: 50px;
    border-radius: 25px;
    padding: 0px 20px;
    background-color: #f7f7f7;
    border: 1px solid #e7e7e7;
    font-size: 14px;
    color: #4a4a4a;
    margin-bottom: 30px;
}

.contact-page .right-content form textarea {
    width: 100%;
    height: 130px;
    border-radius: 25px;
    padding: 20px 20px;
    background-color: #f7f7f7;
    border: 1px solid #e7e7e7;
    font-size: 14px;
    color: #4a4a4a;
    margin-bottom: 30px;
}

.contact-page .right-content form button {
    display: inline-block;
    height: 50px;
    line-height: 50px;
    background-color: #ee626b;
    color: #fff;
    font-size: 15px;
    text-transform: uppercase;
    font-weight: 500;
    padding: 0px 25px;
    border: none;
    border-radius: 25px;
    transition: all 0.3s;
}

.contact-page .right-content form button:hover {
    background-color: #0071f8;
}

/* 
---------------------------------------------
Responsive Style
--------------------------------------------- 
*/

body {
    overflow-x: hidden;
}

@media (max-width: 767px) {
    .header-area {
        top: 0;
    }
    .main-banner .right-image span.offer {
        left: 20px;
        bottom: 20px;
    }
    .cta .subscribe form button {
        position: relative;
        width: 100%;
        margin-top: 15px;
    }
}

@media (max-width: 992px) {
    .header-area .main-nav .logo img {
        max-width: 110px;
    }
    .header-area .main-nav .nav li a {
        padding-left: 10px;
        padding-right: 10px;
    }
    .header-area .main-nav ul.nav {
        margin-top: 0px;
    }
    .background-header .main-nav ul.nav {
        margin-top: 20px;
    }
    .main-banner .caption {
        margin-bottom: 45px;
        text-align: center;
    }
    .main-banner .caption form,
    .main-banner .caption form input {
        max-width: 100%;
    }
    .features .item {
        margin-bottom: 30px;
    }
    .trending .main-button,
    .cta::after,
    .most-played .main-button,
    .related-games .main-button {
        display: none;
    }
    .most-played .section-heading {
        text-align: center;
    }
    .cta .shop {
        padding: 30px;
        margin-bottom: 30px;
    }
    .cta .subscribe {
        padding: 30px;
    }
    .cta .shop .section-heading {
        margin-right: 0px;
    }
    .trending ul.trending-filter li a {
        font-size: 13px;
        padding: 8px 10px;
    }
    .trending ul.trending-filter li {
        margin: 5px 2px;
    }
    .single-product .left-image {
        margin-right: 0px;
        margin-bottom: 45px;
    }
    .tabs-content .nav-link {
        font-size: 16px;
    }
    .tabs-content ul.nav-tabs li {
        margin-right: 15px;
        padding-right: 15px;
    }
    .page-heading {
        padding: 140px 0px 100px 0px;
    }
    .contact-page .right-content {
        margin-left: 0px;
        margin-top: 45px;
    }
}

